
.FilterSelectContainer{
  /* width: calc(100vw - 68.75vw); */
  width: calc(100vw - 70vw);
  /* 画面の高さとTopCantainerコンポーネントの差 */
  height: calc(100vh - 11.375vh);

  /* 中央に寄せる */
  /* display: flex;
  justify-content: center; */
  /* background-color: rgb(40, 40, 40); */
  background-color: rgb(255, 0, 0);

  /* position: relative; */

  /* 右寄せの配置 */
  float: right;
}

/* 左右の矢印ボタン */
.FilterSelectContainer > img.btn{
  width: 3.75vw;
  height: 6.75vh;

  /* top: 0;
  bottom: 0;
  right: 0;
  left: 0; */

  /* 上下中央 */
  margin-top: auto;
  margin-bottom: auto;
}

/* 中央の画像: 選択しているフィルタのプレビュー画像の後ろ */
.FilterSelectContainer > img.focus{
  width: 12.77777778vw;
  height: 23vh;

  /* height: 80%; */

  /* 上下中央 */
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 1%;
  margin-right: 1%;

  display: flex;
}

/* フィルタのプレビュー画像 */
img.preview{
  /* 画像の比率を守ったサイズ設定 */
  object-fit: contain;
  max-width: 80%;
  max-height: 80%;

  /* 中央に寄せる */
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}